import { IMG_UI_PLAYER } from '../constants';
import { renderPlay } from './renderer-play';
import getImageData from './image-data';

const { matchAsset, spriteSheet } = getImageData();

const drawDataText = (
  ctx,
  msg,
  x = 806,
  fontSize = 22,
  y = 350,
  fillStyle = 'rgba(255,255,255,0.7)',
) => {
  const render = () => {
    ctx.font = `${fontSize}px PixelFont`;
    ctx.textAlign = 'left';
    ctx.fillStyle = fillStyle;
    const splittedText = msg.split('\n');
    splittedText.forEach((ln, i) => ctx.fillText(ln, x, y + i * 27));
  };
  document.fonts.load(`${fontSize}px "PixelFont"`).then(render);
};

const printPersonData = (ctx, player) => {
  const info = `
Name:
Occupation:
Cash:
HP:
Ammo:
Armour:
  `;
  const data = `
Joshua Davids
police officer
${player.cash}$
${player.hp * 10}%
${player.ammo}
${player.armour}
  `;
  drawDataText(ctx, info, 806, 22, 84, 'rgba(20,120,100,0.8)');
  drawDataText(ctx, data, 940, 22, 84);
};

const getItemInfo = (item) => {
  if (!item) return '';
  if (item === 'MEDICIN') {
    return `Type: ${item}
Kairos Labs is proud to
present this excellent
piece of medicin.
Wounded? No problem.
Take it and heal 1 HP.`;
  } if (item === 'COMPUTER') {
    return `Type: ${item}
This hacking device from The
Park is legendary. It is said
to be used to be used by the
best cyber cowboys. It can
de/activate lasers.
`;
  } if (item === 'AMMO') {
    return `Type: ${item}
This may not be the best ammo
that can you can buy. But it 
sure is a damn fine piece of
ammo. Now on sale at The
Market of Despair!
`;
  }
};

const getTypeInformation = (type, specialStates) => {
  if (type === 0) {
    return `Type: SOLDIER
Military grade pansar armoury.

Availability: ${specialStates.includes('SOLDIER_') ? '[X]' : '[ ]'}
  `;
  }
  if (type === 1) {
    return `ENGINEER (BASIC)
Knowledge from Dialectics.
Interface remote direction of
a watcher. If watcher gets hurt,
you get hurt.

Availability: ${specialStates.includes('') ? '[X]' : '[ ]'}`;
  }
  return `ENGINEER (ADVANCED)
Knowledge from Golem Industries.
can remote direct a ROBOT

Availability: ${specialStates.includes('ENGINEER_') ? '[X]' : '[ ]'}`;
};

const renderPlayItems = (ctx, assets, state, data) => {
  renderPlay(ctx, assets, state);
  ctx.drawImage(assets[IMG_UI_PLAYER], 0, 0);
  if (state.player.items.length) {
    const formattedItems = Array.from(
      { length: (state.player.items.length % 4) + 1 },
      (_, i) => Array.from(
        { length: 4 },
        (__, j) => ((i * 4) + j < state.player.items.length
          ? state.player.items[(i * 4) + j]
          : null),
      ),
    );
    formattedItems.forEach(
      (row, i) => row.forEach((item, j) => {
        if (!item || item.type === 'MESSAGE') return;
        const xItem = 40 + (j * (75 + 30 + (j * 2)));
        const yItem = 100 + (i * (75 + 30));
        const sheet = spriteSheet[item.type];
        ctx.drawImage(
          assets[matchAsset.ITEMS],
          ...sheet,
          xItem,
          yItem,
          item.w,
          item.h,
        );
      }),
    );
  }
  if (state.itemSelection.currentSelection === 'items') {
    const itemIndex = (state.itemSelection.items.y * 4) + state.itemSelection.items.x;
    const itemInfo = itemIndex < state.player.items.length
      ? getItemInfo(state.player.items[itemIndex].type)
      : '';
    drawDataText(ctx, itemInfo);
    ctx.lineWidth = 3;
    ctx.strokeStyle = 'red';
    ctx.beginPath();
    const x = 36 + (state.itemSelection.items.x * (81 + 30));
    const itemY = 98 + (state.itemSelection.items.y * (81 + 30));
    ctx.rect(x, itemY, 81, 81);
    ctx.stroke();
  }
  if (state.itemSelection.currentSelection === 'mode') {
    const itemIndex = state.itemSelection.mode.y;
    const itemInfo = getTypeInformation(itemIndex, state.player.specialStates);
    drawDataText(ctx, itemInfo);
    ctx.lineWidth = 3;
    ctx.strokeStyle = 'red';
    ctx.beginPath();
    const modeY = 98 + (state.itemSelection.mode.y * (192 + 35));
    ctx.rect(539, modeY, 192, 192);
    ctx.stroke();
  }
  printPersonData(ctx, state.player);
  return {
    fps: 0,
  };
};

const renderPlayerItemsInit = (ctx, assets) => (state, data) => renderPlayItems(
  ctx,
  assets,
  state,
  data,
);
export default renderPlayerItemsInit;
