import {
  IMG_MENU_ARROW,
  IMG_MENU_ITEMS,
  IMG_MENU_HEADER,
  MENU_OPTIONS,
} from '../constants';
import { isSame } from '../utilities';

const drawMenu = (ctx, assets, menuIndex) => {
  ctx.drawImage(assets[IMG_MENU_HEADER], 0, 0);
  ctx.drawImage(assets[IMG_MENU_ITEMS], 0, 0);
  ctx.drawImage(
    assets[IMG_MENU_ARROW],
    465,
    395 + menuIndex * assets[IMG_MENU_ARROW].height - menuIndex * 4,
  );
};

const renderParallax = (ctx, assets) => (layer, i) => {
  ctx.drawImage(assets[i], layer.x, layer.y);
  ctx.drawImage(assets[i], layer.x + assets[i].width, layer.y);
};

const renderMenu = (ctx, assets, state) => {
  const { menu, layers } = state;
  const menuIndex = MENU_OPTIONS.findIndex(isSame(menu));
  layers.forEach(renderParallax(ctx, assets));
  drawMenu(ctx, assets, menuIndex);
};

const renderMenuInit = (ctx, assets) => (state) => {
  renderMenu(
    ctx,
    assets,
    state,
  );
};
export default renderMenuInit;
