import {
  IMG_SCREEN_DISPLAY,
  IMG_NOISE,
} from '../constants';

const drawScreenParagraphText = (ctx, msg, fillStyle = 'rgba(20, 120, 100, 0.8)') => {
  const render = () => {
    ctx.font = '18px PixelFont';
    ctx.textAlign = 'left';
    ctx.fillStyle = fillStyle;
    const splittedText = msg.split('\n');
    splittedText.forEach((ln, i) => ctx.fillText(ln, 140, 140 + i * 27));
  };
  document.fonts.load('20px "PixelFont"').then(render);
};

const drawScreenHeaderText = (ctx, msg, fillStyle = 'rgba(20, 120, 100, 0.8)') => {
  ctx.textAlign = 'left';
  ctx.fillStyle = fillStyle;
  ctx.font = '25px PixelFont';
  ctx.fillText(msg, 140, 130);
};

const drawScreenInstructionText = (ctx, msg, fillStyle = 'rgba(20, 120, 100, 0.8)') => {
  ctx.textAlign = 'left';
  ctx.fillStyle = fillStyle;
  ctx.font = '25px PixelFont';
  ctx.fillText(msg, 870, 130);
};
const drawNoise = (ctx, assets, noiseCount) => {
  ctx.drawImage(assets[IMG_NOISE + noiseCount], 0, 0);
};

const clearScreen = (ctx) => {
  ctx.fillStyle = 'rgba(25,61,65,0.4)';
  ctx.fillRect(0, 0, 1152, 768);
};

const renderText = (ctx, assets, state) => {
  drawNoise(ctx, assets, state.noiseCount);
  clearScreen(ctx);
  drawScreenParagraphText(ctx, `${state.msg}▉`);
  drawScreenInstructionText(ctx, state.text[state.mode].next);
  drawScreenHeaderText(ctx, state.text[state.mode].header);
  ctx.drawImage(assets[IMG_SCREEN_DISPLAY], 0, 0);
};

const renderTextInit = (ctx, assets) => (state) => {
  renderText(ctx, assets, state);
};
export default renderTextInit;
