import {
  PLAYER_FACES_LEFT,
  MODE_GAME_OVER,
  SPRITE_CHANGE,
} from '../constants';
import { compose } from '../utilities';
import getImageData from '../renderers/image-data';
import {
  isAHit,
  initHorisontalMovement,
  checkObstacles,
  finalMovement,
} from './common';

const { spriteSheet } = getImageData();

const handleShotingWatcher = (state, player) => ({
  ...state,
  player: {
    ...player,
    spriteCount: 0,
    ammo: player.ammo - 1,
    isShooting: false,
    remote: {
      ...player.remote,
      spriteCount: 0,
      state: player.remote.faces === PLAYER_FACES_LEFT
        ? 'PLAYER_WATCHER_IDLE_LEFT'
        : 'PLAYER_WATCHER_IDLE_RIGHT',
    },
  },
  level: {
    ...state.level,
    [player.currentScreen]: {
      ...state.level[player.currentScreen],
      shots: [
        ...state.level[player.currentScreen].shots,
        {
          x:
              player.remote.faces === PLAYER_FACES_LEFT
                ? player.remote.x - 50
                : player.remote.x + 120,
          y: player.remote.y + 40,
          w: 18,
          h: 18,
          hasHit: -1,
          hasHitObstacle: false,
          type: 'shot',
          speed: player.remote.faces === PLAYER_FACES_LEFT ? -18 : 18,
          status: true,
        },
      ],
    },
  },
});

const shooting = (state, player) => {
  if (state.player.spriteCount >= 11) {
    return {
      ...state,
      player: {
        ...player,
        spriteCount: 0,
        ammo: player.ammo - 0.5,
        isShooting: false,
        state:
          player.faces === PLAYER_FACES_LEFT
            ? `PLAYER_${player.specialState}IDLE_LEFT`
            : `PLAYER_${player.specialState}IDLE_RIGHT`,
      },
      level: {
        ...state.level,
        [player.currentScreen]: {
          ...state.level[player.currentScreen],
          shots: [
            ...state.level[player.currentScreen].shots,
            {
              x:
              player.faces === PLAYER_FACES_LEFT
                ? player.x - 50
                : player.x + 120,
              y: player.y + 40,
              w: 18,
              h: 18,
              hasHit: -1,
              hasHitObstacle: false,
              type: 'shot',
              speed: player.faces === PLAYER_FACES_LEFT ? -18 : 18,
              status: true,
            },
          ],
        },
      },
    };
  }
  return {
    ...state,
    player: {
      ...player,
      spriteCount: player.spriteCount + 1,
    },
  };
};

const turnPlayerIfNeeded = (player) => {
  const state = player.remote.faces === PLAYER_FACES_LEFT
    ? 'PLAYER_IDLE_LEFT'
    : 'PLAYER_IDLE_RIGHT';
  if (player.velX === 0 && !player.jumping) {
    return {
      ...player,
      state,
    };
  }
  if ((player.velX > 0 || player.velX < 0) && !player.jumping) {
    return {
      ...player,
      state:
        player.faces === PLAYER_FACES_LEFT
          ? `PLAYER_${player.specialState}WALK_LEFT`
          : `PLAYER_${player.specialState}WALK_RIGHT`,
    };
  }
  return player;
};

const modifyHealth = (state, [,, w, h], enemies) => (player) => {
  const playerIsHurt = enemies.some(
    (enemy) => isAHit(
      {
        x: enemy.x - 5,
        y: enemy.y - 5,
        w: enemy.w + 10,
        h: enemy.h + 10,
      },
      {
        x: player.x,
        y: player.y,
        w,
        h,
      },
    ),
  );
  if (
    state.level[player.currentScreen].shots.some((s) => s.hasHitPlayer)
    || playerIsHurt
  ) {
    return {
      ...player,
      state: player.faces === PLAYER_FACES_LEFT
        ? `PLAYER_${player.specialState}HURT_LEFT`
        : `PLAYER_${player.specialState}HURT_RIGHT`,
      hp: player.hp - 1,
      spriteCount: 0,
    };
  }
  return player;
};

const updatePlayerPickup = ([,, w, h], items) => (player) => {
  const y = player.remoteActive
    ? player.remote.y
    : player.y;
  const x = player.remoteActive
    ? player.remote.x
    : player.x;
  const hasHitItem = items.findIndex((item) => isAHit(
    {
      x: item.x,
      y: item.y,
      w: item.w,
      h: item.h,
    },
    {
      x,
      y,
      w,
      h,
    },
  ));
  if (player.remoteActive) {
    if (hasHitItem >= 0) {
      return {
        ...player,
        items: !['SAVE_GAME', 'WATCHER', 'MESSAGE', 'LEVEL'].includes(items[hasHitItem].type)
          ? [...player.items, items[hasHitItem]]
          : player.items,
        remote: {
          ...player.remote,
          hasHitItem,
        },
      };
    }
    return {
      ...player,
      remote: {
        ...player.remote,
        hasHitItem: -1,
      },
    };
  }
  if (hasHitItem >= 0) {
    return {
      ...player,
      items: !['SAVE_GAME', 'WATCHER', 'MESSAGE', 'LEVEL'].includes(items[hasHitItem].type)
        ? [...player.items, items[hasHitItem]]
        : player.items,
      hasHitItem,
    };
  }
  return {
    ...player,
    hasHitItem: -1,
  };
};

const moveWatcherX = (remote, state, [,, watcherWidth, watcherHeight]) => (
  !state.level[state.player.currentScreen].objects.some(([x, y, w, h]) => isAHit({
    x: Math.floor(remote.x + remote.velX), y: remote.y, w: watcherWidth, h: watcherHeight,
  }, {
    x, y, w, h,
  }))
    ? remote.x + remote.velX
    : remote.x
);

const moveWatcherY = (remote, state, [,, watcherWidth, watcherHeight]) => (
  !state.level[state.player.currentScreen].objects.some(
    ([x, y, w, h]) => isAHit({
      y: Math.floor(remote.y + remote.velY),
      x: remote.x,
      w: watcherWidth,
      h: watcherHeight,
    }, {
      x, y, w, h,
    }),
  )
    ? remote.y + remote.velY
    : remote.y);

const handleWatcher = ([,, w, h], state) => (player) => {
  if (player.remote) {
    const sheet = spriteSheet[player.remote.state][Math.floor(player.remote.spriteCount)];
    const maxSpriteCount = spriteSheet[player.remote.state].length;
    const spriteCount = player.remote.spriteCount + SPRITE_CHANGE < maxSpriteCount
      && !(player.remote.spriteCount >= maxSpriteCount)
      ? player.remote.spriteCount + SPRITE_CHANGE
      : 0;
    if (spriteCount >= 12 && player.remote.state.startsWith('PLAYER_SUMMON_WATCHER')) {
      return {
        ...player,
        remoteActiveDone: true,
        remote: {
          ...player.remote,
          x: player.faces === PLAYER_FACES_LEFT
            ? player.remote.x
            : player.remote.x + 50,
          spriteCount: 0,
          state: player.faces === PLAYER_FACES_LEFT
            ? 'PLAYER_WATCHER_IDLE_LEFT'
            : 'PLAYER_WATCHER_IDLE_RIGHT',
        },
      };
    }
    return {
      ...player,
      hp: !state.level[player.currentScreen].enemies.some(
        (enemy) => isAHit(
          {
            x: enemy.x - 5,
            y: enemy.y + 5,
            w: enemy.w + 10,
            h: enemy.h + 10,
          },
          {
            x: player.remote.x,
            y: player.remote.y,
            w: sheet[2],
            h: sheet[3],
          },
        ),
      )
        ? player.hp
        : player.hp - 1,
      remote: {
        ...player.remote,
        x: moveWatcherX(player.remote, state, sheet),
        y: moveWatcherY(player.remote, state, sheet),
        spriteCount,
      },
    };
  }
};

const terminateIfDeath = (player) => (player.hp <= 0
  ? ({
    ...player,
    death: true,
    state: `PLAYER_${player.specialState}DEATH_${player.faces === PLAYER_FACES_LEFT ? 'LEFT' : 'RIGHT'}`,
    spriteCount: 0,
  })
  : player);

const updatePlayer = ({ state, assets }) => {
  const maxSpriteCount = spriteSheet[state.player.state].length;
  const spriteCount = state.player.state.includes('JUMP')
    ? state.player.spriteCount + SPRITE_CHANGE < maxSpriteCount
    && !(state.player.spriteCount >= maxSpriteCount)
      ? state.player.spriteCount + SPRITE_CHANGE
      : 5
    : state.player.spriteCount + SPRITE_CHANGE < maxSpriteCount
    && !(state.player.spriteCount >= maxSpriteCount)
      ? state.player.spriteCount + SPRITE_CHANGE
      : 0;
  if (state.player.death && state.player.spriteCount >= maxSpriteCount - 1) {
    return {
      assets,
      state: {
        ...state,
        mode: MODE_GAME_OVER,
      },
    };
  }
  if (state.player.death) {
    return {
      assets,
      state: {
        ...state,
        player: {
          ...state.player,
          spriteCount,
        },
      },
    };
  }
  if (state.player.state.includes('HURT') && state.player.spriteCount >= maxSpriteCount - 1) {
    return {
      assets,
      state: {
        ...state,
        player: {
          ...state.player,
          state: state.player.faces === PLAYER_FACES_LEFT
            ? `PLAYER_${state.player.specialState}IDLE_LEFT`
            : `PLAYER_${state.player.specialState}IDLE_RIGHT`,
          spriteCount: 0,
        },
      },
    };
  }
  if (state.player.state.includes('HURT') && state.player.spriteCount < maxSpriteCount) {
    return {
      assets,
      state: {
        ...state,
        player: {
          ...state.player,
          spriteCount,
          y: state.player.y - 0.5,
          x: state.player.faces === PLAYER_FACES_LEFT
            ? state.player.x + 8
            : state.player.x - 8,
        },
      },
    };
  }
  if (state.player.state.startsWith('PLAYER_SUMMON')) {
    if (state.player.spriteCount >= 12) {
      return {
        assets,
        state: {
          ...state,
          player: {
            ...state.player,
            spriteCount: 0,
            state: state.player.faces === PLAYER_FACES_LEFT
              ? 'PLAYER_IDLE_LEFT'
              : 'PLAYER_IDLE_RIGHT',
            remote: {
              ...state.player.remote,
              x: state.player.faces === PLAYER_FACES_LEFT
                ? state.player.x - 100
                : state.player.x + 100,
              y: state.player.y,
              spriteCount: 0,
              faces: state.player.faces === PLAYER_FACES_LEFT
                ? PLAYER_FACES_LEFT
                : 'PLAYER_FACES_RIGHT',
              state: state.player.faces === PLAYER_FACES_LEFT
                ? 'PLAYER_SUMMON_WATCHER_LEFT'
                : 'PLAYER_SUMMON_WATCHER_RIGHT',
            },
          },
        },
      };
    }
    return {
      assets,
      state: {
        ...state,
        player: {
          ...state.player,
          spriteCount,
        },
      },
    };
  }
  if (state.player.isShooting) {
    return {
      state: shooting(state, state.player),
      assets,
    };
  }
  if (
    state.player.remote.spriteCount >= 15
    && state.player.remote.state.startsWith('PLAYER_WATCHER_ATTACK')
  ) {
    return {
      state: handleShotingWatcher(state, state.player),
      assets,
    };
  }
  const player = {
    ...state.player,
    grounded: false,
    spriteCount,
  };
  return {
    state: {
      ...state,
      player: compose(
        terminateIfDeath,
        handleWatcher(
          spriteSheet[player.remote.state][Math.floor(player.remote.spriteCount)],
          state,
        ),
        updatePlayerPickup(
          spriteSheet[player.state][Math.floor(player.spriteCount)],
          state.level[player.currentScreen].items,
        ),
        modifyHealth(
          state,
          spriteSheet[player.state][Math.floor(player.spriteCount)],
          state.level[player.currentScreen].enemies.filter((e) => ['TYPE_1'].includes(e.type)),
        ),
        finalMovement,
        turnPlayerIfNeeded,
        (plr) => (plr.grounded ? { ...plr, velY: 0 } : plr),
        checkObstacles(
          spriteSheet[player.state][Math.floor(player.spriteCount)],
          [
            ...state.level[player.currentScreen].enemies
              .filter((e) => ['TYPE_1'].includes(e.type))
              .map(
                (enemy) => [enemy.x, enemy.y, enemy.w, enemy.h],
              ),
            ...state.level[player.currentScreen].objects,
            ...state.level[player.currentScreen].actionItems
              .filter((itm) => itm.type !== 'laser' && itm.img !== 'DOOR')
              .map((itm) => [itm.x, itm.y, itm.w, itm.h]),
          ],
        ),
        initHorisontalMovement,
      )(player),
    },
    assets,
  };
};
export default updatePlayer;
