import { compose } from '../utilities';
import {
  isAHit,
  hitAreas,
  updateEntity,
} from './common';
import {
  ENEMY_FACES_LEFT,
  ENEMY_FACES_RIGHT,
  PLAYER_FACES_LEFT,
  PLAYER_WATCHER_SPECIAL_STATE,
} from '../constants';

const retrieveMessage = (player, items) => {
  const hasHitItem = player.hasHitItem > -1
    ? player.hasHitItem
    : player.remote.hasHitItem;
  if (items[hasHitItem].type === 'WATCHER') {
    return {
      active: true,
      count: 20,
      message: 'NEW SKILL: ENGINEER (BASIC)',
    };
  }
  if (items[hasHitItem].type === 'COMPUTER') {
    return {
      active: true,
      count: 20,
      message: 'COMPUTER',
    };
  }
  if (items[hasHitItem].type === 'GUN') {
    return {
      active: true,
      count: 20,
      message: 'BETTER SHOTS',
    };
  }
  if (items[hasHitItem].type === 'ARMOUR') {
    return {
      active: true,
      count: 20,
      message: 'ARMOUR 1+',
    };
  }
  if (items[hasHitItem].type === 'HELMET') {
    return {
      active: true,
      count: 20,
      message: 'ARMOUR 1+',
    };
  }
  if (items[hasHitItem].type === 'AMMO') {
    return {
      active: true,
      count: 20,
      message: 'AMM0 1+',
    };
  }
  if (items[hasHitItem].type === 'MEDICIN') {
    return {
      active: true,
      count: 20,
      message: 'MEDICIN 1+',
    };
  }
  return {
    active: true,
    count: 120,
    message: items[hasHitItem].message,
  };
};

const setSpeed = (actionItem) => {
  if (actionItem.type === 'vertical elevator') {
    if (actionItem.y + actionItem.speed >= actionItem.maxY) {
      return actionItem.speed * -1;
    } if (actionItem.y + actionItem.speed <= actionItem.startY) {
      return actionItem.speed * -1;
    }
    return actionItem.speed;
  }
  if (actionItem.type === 'horisontal elevator') {
    if (actionItem.x + actionItem.speed >= actionItem.maxX) {
      return actionItem.speed * -1;
    } if (actionItem.x + actionItem.speed <= actionItem.startX) {
      return actionItem.speed * -1;
    }
    return actionItem.speed;
  }
  return actionItem.speed;
};

const moveActionItem = (isPlayerOnItem, actionItem) => {
  if (actionItem.type === 'vertical elevator') {
    return {
      y: isPlayerOnItem ? actionItem.y + actionItem.speed : actionItem.y,
    };
  }
  if (actionItem.type === 'horisontal elevator') {
    return {
      x: isPlayerOnItem ? actionItem.x + actionItem.speed : actionItem.x,
    };
  }
  return actionItem;
};

const handleItems = (state) => {
  if (
    state.player.hasHitItem > -1
    && state.level[state.player.currentScreen].items[state.player.hasHitItem].type === 'WATCHER'
  ) {
    const player = {
      ...state.player,
      specialStates: [...state.player.specialStates, PLAYER_WATCHER_SPECIAL_STATE],
    };
    return {
      ...state,
      player,
      level: {
        ...state.level,
        [state.player.currentScreen]: {
          ...state.level[state.player.currentScreen],
          messages: state.player.hasHitItem > -1 || state.player.remote.hasHitItem > -1
            ? [
              ...state.level[state.player.currentScreen].messages,
              retrieveMessage(state.player, state.level[state.player.currentScreen].items),
            ]
            : state.level[state.player.currentScreen].messages,
          items: state.level[state.player.currentScreen].items.filter(
            (item, i) => i !== state.player.hasHitItem && i !== state.player.remote.hasHitItem,
          ),
        },
      },
    };
  }
  if (
    state.player.hasHitItem > -1
    && state.level[state.player.currentScreen].items[state.player.hasHitItem].type === 'LEVEL'
  ) {
    localStorage.setItem('load', 'false');
    localStorage.setItem('state', JSON.stringify(state));
    const currentScreen = state.level[state.player.currentScreen].items[state.player.hasHitItem].next;
    const level = {
      ...state.level,
      [currentScreen]: {
        ...state.level[currentScreen],
        messages: [
          ...state.level[currentScreen].messages,
          {
            active: true,
            count: 100,
            message: 'GAME SAVED',
          },
        ],
      },
    };
    return {
      ...state,
      mode: state.level[state.player.currentScreen].items[state.player.hasHitItem].mode,
      level,
      player: {
        ...state.player,
        currentScreen,
        spriteCount: 0,
        x: state.level[state.player.currentScreen].items[state.player.hasHitItem].startX,
        y: state.level[state.player.currentScreen].items[state.player.hasHitItem].startY,
      },
    };
  }
  return {
    ...state,
    level: {
      ...state.level,
      [state.player.currentScreen]: {
        ...state.level[state.player.currentScreen],
        messages: state.player.hasHitItem > -1 || state.player.remote.hasHitItem > -1
          ? [
            ...state.level[state.player.currentScreen].messages,
            retrieveMessage(state.player, state.level[state.player.currentScreen].items),
          ]
          : state.level[state.player.currentScreen].messages,
        items: state.level[state.player.currentScreen].items.filter(
          (item, i) => i !== state.player.hasHitItem && i !== state.player.remote.hasHitItem,
        ),
      },
    },
  };
};

const handleShots = (state) => ({
  ...state,
  level: {
    ...state.level,
    [state.player.currentScreen]: {
      ...state.level[state.player.currentScreen],
      shots: state.level[state.player.currentScreen].shots
        .map((shot) => ({
          ...shot,
          x: shot.x + shot.speed,
          hasHitPlayer: isAHit(
            {
              x: state.player.x,
              y: state.player.y,
              w: 100,
              h: 100,
            },
            {
              x: shot.x,
              y: shot.y,
              w: shot.w,
              h: shot.h,
            },
          ) || (state.player.remoteActive
            && isAHit(
              {
                x: state.player.remote.x,
                y: state.player.remote.y,
                w: 100,
                h: 100,
              },
              {
                x: shot.x,
                y: shot.y,
                w: shot.w,
                h: shot.h,
              },
            )
          ),
          hasHit: state.level[state.player.currentScreen].enemies.findIndex(
            (enemy) => isAHit(
              {
                x: enemy.x,
                y: enemy.y,
                w: enemy.w,
                h: enemy.h,
              },
              {
                x: shot.x,
                y: shot.y,
                w: shot.w,
                h: shot.h,
              },
            ),
          ),
          hasHitObstacle: state.level[state.player.currentScreen].objects.some(
            ([x, y, width, height]) => isAHit(
              {
                x,
                y,
                w: width,
                h: height,
              },
              {
                x: shot.x,
                y: shot.y,
                w: shot.w,
                h: shot.h,
              },
            ),
          ),
          status: shot.hasHit < 0 && !shot.hasHitObstacle && !shot.hasHitPlayer,
        }))
        .filter((shot) => shot.status),
    },
  },
});

const handleHits = (state) => ({
  ...state,
  level: {
    ...state.level,
    [state.player.currentScreen]: {
      ...state.level[state.player.currentScreen],
      hits: state.level[state.player.currentScreen].shots.filter(
        (shot) => shot.hasHit >= 0 || shot.hasHitObstacle,
      ),
    },
  },
});

const handleExplosions = (state) => ({
  ...state,
  audio: {
    ...state.audio,
    explosion: state.level[state.player.currentScreen].hits.length && !state.audio.explosion,
  },
  level: {
    ...state.level,
    [state.player.currentScreen]: {
      ...state.level[state.player.currentScreen],
      explosions: [
        ...state.level[state.player.currentScreen].hits.map(
          (hit) => (
            hit.hasHit >= 0 ? {
              x: hit.x - 128,
              y: hit.y - 128,
              w: 128 * 2,
              h: 128 * 2,
              status: true,
              spriteCount: 0,
            }
              : {
                x: hit.x - (128 * 1.5) / 2,
                y: hit.y - (128 * 1.5) / 2,
                w: 128 * 1.5,
                h: 128 * 1.5,
                status: true,
                spriteCount: 0,
              }
          ),
        ),
        ...state.level[state.player.currentScreen].explosions.map(
          (explosion) => ({
            ...explosion,
            spriteCount: explosion.spriteCount + 0.5,
            status: explosion.spriteCount < 11,
          }),
        ),
      ],
    },
  },
});

const updateHp = (player, hits, i, enemy) => {
  const hitE = hits.some(
    (hit) => hit.hasHit === i,
  );
  if (hitE) {
    return {
      ...enemy,
      x: player.faces === PLAYER_FACES_LEFT
        ? enemy.x - 100
        : enemy.x + 100,
      y: enemy.y - 80,
      hp: enemy.hp - player.hurt,
    };
  }
  return enemy;
};

const shotOnSight = (enemy, state) => {
  if (enemy.type.includes('TYPE_1')) return enemy;
  const isPlayerInSightLeft = isAHit(
    {
      y: enemy.y - 300,
      x: enemy.x - 300,
      w: enemy.w + 300,
      h: enemy.h + 600,
    },
    {
      x: state.player.x,
      y: state.player.y,
      w: 100,
      h: 100,
    },
  );
  if (isPlayerInSightLeft) {
    return {
      ...enemy,
      spriteCount: 0,
      shotSpeed: -25,
      state: 'ATTACK_LEFT',
    };
  }
  const isPlayerInSightRight = isAHit(
    {
      y: enemy.y - 300,
      x: enemy.x,
      w: enemy.w + 300,
      h: enemy.h + 600,
    },
    {
      x: state.player.x,
      y: state.player.y,
      w: 100,
      h: 100,
    },
  );
  if (isPlayerInSightRight) {
    return {
      ...enemy,
      spriteCount: 0,
      shotSpeed: 25,
      state: 'ATTACK_RIGHT',
    };
  }
  return {
    ...enemy,
    state: enemy.faces === ENEMY_FACES_LEFT ? 'LEFT' : 'RIGHT',
  };
};

const handleEnemies = (state) => {
  const enemies = state.level[state.player.currentScreen].enemies
    .map(
      (enemy) => (enemy.state.includes('ATTACK')
        ? {
          ...enemy,
          ...updateEntity(enemy, state.level[state.player.currentScreen].objects),
          x: enemy.x,
          shotInterval: enemy.shotInterval + 1,
        }
        : {
          ...enemy,
          ...updateEntity(enemy, state.level[state.player.currentScreen].objects),
          faces: enemy.speed > 0
            ? ENEMY_FACES_RIGHT
            : ENEMY_FACES_LEFT,
          state: enemy.faces === ENEMY_FACES_LEFT ? 'LEFT' : 'RIGHT',
        }),
    )
    .map(
      (enemy, i) => updateHp(state.player, state.level[state.player.currentScreen].hits, i, enemy),
    )
    .map((enemy) => shotOnSight(enemy, state))
    .filter((e) => e.hp > 0);
  const shooters = enemies.filter((e) => e.shotInterval % 50 === 1);
  return {
    ...state,
    level: {
      ...state.level,
      [state.player.currentScreen]: {
        ...state.level[state.player.currentScreen],
        enemies,
        shots: shooters.length > 0
          ? [
            ...state.level[state.player.currentScreen].shots,
            ...shooters.map((s) => ({
              x: s.shotSpeed < 0
                ? s.x - 100
                : s.x + 180,
              y: s.y + 40,
              w: 18,
              h: 18,
              hasHitPlayer: false,
              hasHit: -1,
              hasHitObstacle: false,
              type: 'shot',
              speed: s.shotSpeed,
              status: true,
            })),
          ]
          : state.level[state.player.currentScreen].shots,
      },
    },
  };
};

const handleActionItems = (player) => (state) => {
  let newPlayer;
  const nextState = {
    ...state,
    level: {
      ...state.level,
      [state.player.currentScreen]: {
        ...state.level[state.player.currentScreen],
        actionItems: state.level[state.player.currentScreen].actionItems.map((actionItem) => {
          const isPlayerOnItem = isAHit(
            {
              ...actionItem,
              y: actionItem.y - 10,
            },
            {
              x: player.x,
              y: player.y,
              w: player.w,
              h: player.h,
            },
          );
          if (
            isPlayerOnItem
            && actionItem.type === 'laser'
            && actionItem.status
            && actionItem.spriteCount > 2
          ) {
            newPlayer = {
              ...state.player,
              hp: state.player.hp - 0.5,
              x: state.player.faces === PLAYER_FACES_LEFT
                ? state.player.x + 20
                : state.player.x - 20,
            };
          }
          if (isPlayerOnItem && actionItem.type === 'vertical elevator') {
            newPlayer = {
              ...state.player,
              y: state.player.y + actionItem.speed,
            };
          }
          if (isPlayerOnItem && actionItem.type === 'horisontal elevator') {
            newPlayer = {
              ...state.player,
              x: state.player.x + actionItem.speed,
            };
          }
          return {
            ...actionItem,
            ...moveActionItem(isPlayerOnItem, actionItem),
            spriteCount: actionItem.spriteCount + 1 > actionItem.maxSpriteCount
              ? 0
              : actionItem.spriteCount + 1,
            speed: setSpeed(actionItem),
          };
        }),
      },
    },
  };
  if (newPlayer) {
    return {
      ...nextState,
      player: newPlayer,
    };
  }
  return nextState;
};

const updateThings = ({ state, assets }) => ({
  state: compose(
    handleActionItems({
      x: state.player.x,
      y: state.player.y,
      w: 80,
      h: 130,
    }),
    handleItems,
    handleShots,
    handleHits,
    handleExplosions,
    handleEnemies,
  )(state),
  assets,
});
export default updateThings;
